body {
  background-color: #1B1C1E;
}

.app__Search {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #1B1C1E; /* or any other dark color */
}

h1 {
  text-align: center;
  font-size: 32px;
  margin-bottom: 20px;
}

.titleBooks{
  color: #FBFCFC;
}

.app__search-container {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.searchbar {
  position: relative;
  padding: 10px;
  padding-left: 35px;
  font-size: 16px;
  width: 100%;
  border: none;
  border-radius: 20px;
  background-color: #3C4043;
  outline: none;
}

.searchbar:hover,
.searchbar:focus {
  background-color: #A7ABB7;
}

.searchbar::placeholder {
  color: #999;
}


.app__book-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 20px;
}

.book-item {
  background-color: #202125;
  border-radius: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.book-cover {
  width: 150px;
  height: auto;
  margin-bottom: 10px;
}

.book-title {
  font-size: 16px;
  color: white;
  text-decoration: none;
  transition: color 0.3s ease;
}

.book-title:hover {
  color: whitesmoke;
  font-weight: bold;
}

.clear-input {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translate(-50%, -50%);
  color: #9194a3;
  cursor: pointer;
}

.clear-input:hover {
  color: #000;
}


.search-icon {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  color: #9194a3;
  z-index: 1;
  cursor: pointer;
}


@media screen and (max-width: 450px) {
  .app__search-container {
    flex-direction: column;
    align-items: stretch;
    position: relative;
  }

  .search-icon,
  .clear-input {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    color: #9194a3;
    cursor: pointer;
  }

  .search-icon {
    left: 10px;
    z-index: 1;
  }
  .clear-input {
    right: 10px;
  }

  .searchbar {
    margin-bottom: 10px;
    width: 90%;
    padding-left: 35px;
  }
  .app__book-list{
    grid-template-columns: 1fr;
  }

  .book-item {
    padding: 5px;
  }

  .book-cover {
    width: 100px;
  }

  .book-title {
    font-size: 14px;
  }
}
